import React from 'react';

import artPieces from '../UserCart/ArtPieces';

import ItemCard from './ItemCard';

import weirdpeople from './weirdpeople.jpg';

import './Shop.css'

const Shop = (props) => {

    function handleClick(event) {
        props.addToCart(event.target.parentElement);
    };

    return (
        <div className='shopPage'>
            <img onClick={handleClick} className='weirdpeople' alt='artsy stuff' src={weirdpeople}></img>
            <div className='latin'>Vous pouvez convaincre n'importe qui que n'importe quel texte est vraiment profond et 
                                    significatif en le mettant dans une autre langue. 
                                    Tu as pris le temps de traduire ça ? Drôle.
            </div>
            <div className='itemDisplay'>
                {artPieces.map((piece) => <ItemCard onClick={handleClick} key={piece.id} id={piece.id} title={piece.name} price={piece.price} quantity={piece.quantity} imgSrc={piece.pic} />)}
            </div>
        </div>
    )
};

export default Shop 