import React from 'react';

import './cart.css';

const CartItem = (props) => {

    return (
        <div className='cartItem' id={props.id}>
            <div className='cartImg'>
                <img className='cartItemImg' alt={props.id} src={props.prodImg}></img>
            </div>
            <div className='cartText'>
                <div className='cartItemTitle'>{props.prodName}</div>
                <div className='cartItemPrice'>{props.prodPrice}</div>
            </div>
            <div className='cartQuantity'>
                <div className='cartItemButtons'>
                    <button onClick={props.onDel} className='quantity' disabled={props.quantity < 2}>-</button>
                    <input className='quantityInput' type='text' value={props.quantity}></input>
                    <button onClick={props.onAdd} className='quantity'>+</button>
                </div>
                <div>
                    <button onClick={props.removeItem} className='cartDelete'>Delete</button>
                </div>

            </div>
        </div>
    )
};

export default CartItem