import React, { useState } from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import Navbar from './Navbar/Navbar'
import Home from './Home/Home';
import Shop from './Shop/Shop';
import Cart from './Cart/cart';
import Contact from './Contact/Contact';

import artPieces from './UserCart/ArtPieces';

function App() {

  let [cart, setCart] = useState([]);

  function addToCart(art) {

    console.log(art);
    const isDuplicate = cart.map(item => item.id).includes(art.id);

    for (let i = 0; i < artPieces.length; i++) {
      if (artPieces[i].id === art.id) {
        if (isDuplicate) {
          setCart(cart.map(item => (item.id === art.id ? {...item, quantity: item.quantity + 1} : item)));
        }
        else {
          setCart([...cart, artPieces[i]]);
        }
      }
    }
  };

  function handleAdd(event) {
    let clickedItem = event.target.parentElement.parentElement.parentElement;
    setCart(cart.map(item => (item.id === clickedItem.id ? {...item, quantity: item.quantity + 1} : item)));
  }

  function handleDel(event) {
    let clickedItem = event.target.parentElement.parentElement.parentElement;
    setCart(cart.map(item => (item.id === clickedItem.id ? {...item, quantity: item.quantity - 1} : item)));
  }

  function handleRemove(event) {
    let clickedItem = event.target.parentElement.parentElement.parentElement;
    setCart(cart.filter(item => item.id !== clickedItem.id));
  }


  return (
    <Router>
      <div className='App'>
        <Navbar cart={cart} />
        <div className='content'>
          <Switch>
            <Route exact path='/'>
              <Home />
            </Route>
            <Route exact path='/shop'>
              <Shop addToCart={addToCart} />
            </Route>
            <Route exact path='/cart'>
              <Cart cart={cart} onAdd={handleAdd} onDel={handleDel} remove={handleRemove} />
            </ Route>
            <Route exact path='/contact'>
              <Contact />
            </Route>
          </Switch>

        </div>
      </div>
    </Router>
  );
}

export default App;
