import React from 'react';

import './ItemCard.css';

const ItemCard = (props) => {

    return (
        <div className='itemCard' id={props.id}>
            <div className='productpic'>
                <img alt='painting' className='art' src={props.imgSrc}></img>
            </div>
            <div className='productText'>
                <div className='productName'>{props.title}</div>
                <div className='productPrice'>{props.price}</div>
            </div>
            <span id='addCart' onClick={props.onClick} className='material-icons'>
                add_shopping_cart
            </span>
        </div>
    )
}

export default ItemCard