import React from 'react';

import {Link} from 'react-router-dom';

import CartItem from './cartItem';

import './cart.css';

const Cart = (props) => {

    const cartTitle ='Shopping Cart';

    let orderTotal = props.cart.map(item => item.numPrice * item.quantity).reduce((a,b) => a + b, 0); 


    return (
        <div className='cartDisplay'>
            <div className='cartDiv'>
                <div className='cartBackground'>
                    <div className='cartTitle'>{cartTitle}</div>
                    <div className='cart'>
                        {props.cart.map((item) => <CartItem key={item.id} id={item.id} prodName={item.name} prodPrice={item.price} quantity={item.quantity} prodImg={item.pic} onAdd={props.onAdd} onDel={props.onDel} removeItem={props.remove}/>)}
                    </div>
                    <div className='total'>
                        <div className='orderTotal'>Order Total: ${orderTotal}</div>
                    </div>
                    <div className='cartButtons'>
                        <Link to='./shop' className='checkoutButton'>Back to Shop</Link>
                        <a href='https://www.youtube.com/watch?v=51Me5a-Iiew' className='checkoutButton'>Checkout</a>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default Cart